nav {
  background-color: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}

nav a:hover {
  background-color: rgba(0, 0, 0, 0.4);
}

nav a.active {
  background: var(--color-primary);
  color: var(--color-bg);
}

/* ============== MEDIA QUERIES (SMALL DEVICES) ============= */
@media screen and (max-width: 600px) {
  nav {
    width: 90%;
    padding: 0.7rem;
    gap: 0.5rem;
    justify-content: center;
  }

  nav a {
    padding: 0.4rem;
    font-size: 1rem;
  }
}
